<template>
  <div class>
    <Editor
      :init="init"
      v-model="content"
    ></Editor>
  </div>
</template>

<script>
import Editor from "@tinymce/tinymce-vue"; //tinymce  npm 
import tinymce from "./tinymce.min.js"; //tinymce 
import "./icons/default/icons.min.js";
import "./themes/silver/theme.min.js";
import "./langs/zh-Hans.js";

//
import "./plugins/preview/plugin.min.js"; // 
import "./plugins/paste/plugin.min.js";
import "./plugins/wordcount/plugin.min.js"; // 
import "./plugins/code/plugin.min.js"; // 

import "./plugins/image/plugin.min.js";
import "./plugins/imagetools/plugin.min.js";
import "./plugins/media/plugin.min.js";
import "./plugins/codesample/plugin.min.js";
import "./plugins/lists/plugin.min.js";
import "./plugins/table/plugin.min.js";

export default {
  name: "",
  components: { Editor },
  props: {
    value: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      //
      init: {
        language: "zh_CN",
        height: "500px",
        //
        plugins:
          "preview paste wordcount code imagetools image media codesample lists table",
        margin: "0",
        padding: "0",
        branding: false, //'powered by tinymce' 
        //toolbar 
        toolbar: [
          "undo redo | styleselect |fontsizeselect| bold italic underline strikethrough |alignleft aligncenter alignright alignjustify |blockquote removeformat |numlist bullist table",
          "preview paste code codesample |image media",
        ],
        // 
        images_upload_handler: async (blobInfo, success, failure) => {
          let formData = new FormData();
          formData.append("file", blobInfo.blob(), blobInfo.filename()); // doc  filename(),Bname().

          const { data: res } = await this.$axios.post("upload", formData);

          success(res.url);
          failure(this.$message.error("tinymce "));
        },
        imagetools_cors_hosts: ["*"],
        imagetools_proxy: "*",
      },
      //  AddArt.vue <Editor>
      content: this.value,
    };
  },
  watch: {
    value(newVal) {
      this.content = newVal;
    },
    content(newVal) {
      this.$emit("input", newVal);
    },
  },
};
</script>

<style>
@import url("./skins/ui/oxide/skin.min.css");
@import url("./skins/content/default/content.min.css");
</style>

