<template>
  <div>
    <a-card>
      <!---->
      <a-row :gutter="16">
        <a-col :span="6">
          <a-input-search
            placeholder=""
            enter-button
            v-model="queryParam.title"
            @search="getArtList"
            allowClear
          />
        </a-col>
        <a-col :span="4">
          <!--vue<template><script>this.-->
          <a-button
            type="primary"
            @click="handleAddArt"
          ></a-button>
        </a-col>

        <a-col :span="8">
          <a-select
            show-search
            allowClear
            placeholder=""
            style="width: 200px"
            @change="handleCateChange"
          >
            <!--@change value  :value -->
            <!-- v-for  in/of  -->
            <a-select-option
              v-for="item in cateList"
              :key="item.id"
            >{{item.name}}</a-select-option>
          </a-select>
        </a-col>
      </a-row>
      <!--/-->

      <!---->
      <a-table
        :dataSource="articleList"
        :columns="columns"
        :pagination="pagination"
        bordered
        rowKey="ID"
        @change="handleTableChange"
      >
        <span
          class="artImg"
          slot="img"
          slot-scope="img"
        >
          <img
            :src="img"
            class="artImgItem"
          />
        </span>

        <template
          slot="action"
          slot-scope="data"
        >
          <div class="actionSlot">
            <!--push()'/'-->
            <a-button
              type="primary"
              @click="$router.push(`/addart/${data.ID}`)"
              style="margin-right: 15px"
            ></a-button>

            <a-button
              type="danger"
              @click="deleteConfirm(data.ID)"
              style="margin-right: 15px"
            ></a-button>
          </div>
        </template>
      </a-table>
      <!--/-->
    </a-card>
  </div>
</template>
    
<script>
const columns = [
  {
    title: "ID",
    dataIndex: "ID", //jsonjson
    sorter: true,
    width: "5%",
    align: "center",
    key: "id",

    scopedSlots: { customRender: "id" }, //Vue<slot name="xxx">
  },
  {
    title: "",
    /*json AntDV 
      v1 
         key a.b.c 
      v3 
      ... dataIndex  user.age 
           ['user', 'age']
           . 
    */
    dataIndex: "Category.name",
    width: "10%",
    align: "center",
    key: "name",
  },
  {
    title: "",
    dataIndex: "title",
    align: "center",
    width: "20%",
    key: "title",
  },
  {
    title: "",
    dataIndex: "desc",
    width: "20%",
    align: "center",
    key: "desc",
  },
  {
    title: "",
    dataIndex: "image",
    width: "20%",
    align: "center",
    key: "image",
    scopedSlots: { customRender: "img" },
  },
  {
    title: "",
    width: "30%",
    align: "center",
    key: "action",
    scopedSlots: { customRender: "action" },
  },
];

export default {
  name: "AdminIndex",

  data() {
    return {
      //
      articleList: [],
      //
      cateList: [],

      //
      columns,

      //,getArtList() axios 
      queryParam: {
        title: "",
        pagesize: 5,
        pagenum: 1,
      },

      //
      pagination: {
        pageSizeOptions: ["5", "10", "20"], //
        pageSize: 5, ///
        current: 1, //
        total: 0, //
        showSizeChanger: true, //
        showTotal: (total, range) => {
          //
          //range[0]
          //range[1]
          return ` ${total}   `;
        },
      },
      //&
      labelCol: { span: 6 },
      wrapperCol: { span: 14, offset: 1 },
      //&
      rules: {
        //[!]
        title: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 4,
            max: 12,
            message: "4-12",
            trigger: "blur",
          },
        ],
      },
    };
  },

  created() {
    //
    this.getArtList();
    this.getCateList();
  },

  methods: {
    //
    async getArtList() {
      //datares.
      const { data: res } = await this.$axios.get("articles", {
        params: {
          title: this.queryParam.title, //""
          pagenum: this.queryParam.pagenum,
          pagesize: this.queryParam.pagesize,
        },
      });

      if (res.status != 200) return this.$message.error(res.message);

      //
      this.articleList = res.data;
      this.pagination.total = res.total;
    },

    //
    async getCateList() {
      //datares.
      const { data: res } = await this.$axios.get("categories");

      if (res.status != 200) return this.$message.error(res.message);

      //
      this.cateList = res.data;
      this.pagination.total = res.total;
    },
    //==========================================

    //getCateArt
    handleCateChange(value) {
      /* value  <a-select-option> :value 
         :value , :key  item.id
      */
      //
      value != undefined ? this.getCateArt(value) : this.getArtList();
    },
    // 
    async getCateArt(id) {
      const { data: res } = await this.$axios.get(`article/list/${id}`);

      if (res.status != 200) return this.$message.error(res.message);

      this.articleList = res.data;
      this.pagination.total = res.total;
    },

    //Table (onChange antdv API,)
    handleTableChange(pagination, filters, sorter) {
      /* pagination 
         current:
         pageSize:
         total: 
      */

      // pager 
      // this.pagination 
      let pager = { ...this.pagination };

      //1. pagination 
      pager.current = pagination.current;
      pager.pageSize = pagination.pageSize;

      //2. pagination 
      this.queryParam.pagenum = pagination.current;
      this.queryParam.pagesize = pagination.pageSize;

      //3.==
      if (pagination.pageSize !== this.pagination.pageSize) {
        pager.current = 1;
        this.queryParam.pagenum = 1;
      }

      //4.
      this.pagination = pager;

      //5.
      this.getArtList();
    },
    //==========================================
    //
    async deleteArt(id) {
      // <a-table> data getArtList()
      /*  CreatedAt
          DeletedAt
          ID
          UpdatedAt
          password
          role
          title
      */
      // console.log("<a-table>:", data);
      const res = await this.$axios.delete(`article/${id}`);
      if (res.status !== 200) {
        return this.$message.error(res.message);
      }
      this.$message.success("");

      //5.
      this.getArtList();
    },
    //,deleteArt()
    deleteConfirm(id) {
      this.$confirm({
        title: "?",
        content: "",
        okText: "",
        okType: "primary",
        cancelText: "",
        onOk: () => {
          this.deleteArt(id); //this Vue this 
        },
        onCancel: () => {
          this.$message.info("");
        },
      });
    },
    //==========================================
    //,
    handleAddArt() {
      this.$router.push("/addart").catch((err) => {
        console.log(err);
      });
    },
    //==========================================
    //
    editArt(data) {
      // console.log(data); //data

      //1. data v-model
      this.articleEdit.id = data.ID;
      this.articleEdit.title = data.title;
      this.articleEdit.password = data.password;
      this.articleEdit.role = data.role;

      //2.
      this.editArtVisible = true;

      //3.
    },
    // 
    handleEditArtOK() {
      // validate 
      this.$refs.editArtRef.validate(async (valid) => {
        if (!valid) return this.$message.error(""); // return

        //axios API
        const { data: res } = await this.$axios.put(
          `article/${this.articleEdit.id}`,
          {
            title: this.articleEdit.title,
            role: this.articleEdit.role,
          }
        );

        if (res.status !== 200) return this.$message.error(res.message);
        this.editArtVisible = false; //
        // this.$refs.editArtRef.resetFields(); //
        this.$message.success(""); //
        this.getArtList(); //
      });
    },
    //[!] 
    handleEditArtCancel() {
      this.editArtVisible = false;
      // this.$refs.editArtRef.resetFields();//
      this.$message.info("");
    },
    //==========================================
  },
};
</script>
    
<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}

.artImg {
  display: flex;
  justify-content: center;
}
.artImg img {
  height: 55px;
  width: 55px;
}
</style>
