<template>
  <div>
    <a-card>
      <h3>{{id?"":""}}</h3>

      <a-form-model
        :model="artInfo"
        :rules="rules"
        ref="artInfoRef"
        :hideRequiredMark="true"
      >
        <a-form-model-item
          label=""
          prop="title"
        >
          <a-input
            style="width: 300px;"
            v-model="artInfo.title"
          ></a-input>
        </a-form-model-item>

        <a-form-model-item
          label=""
          prop="category"
        >
          <a-select
            placeholder=""
            v-model="artInfo.cid"
          >
            <a-select-option
              v-for="item in cateList"
              :key="item.id"
            >{{item.name}}</a-select-option>
          </a-select>
        </a-form-model-item>

        <a-form-model-item
          label=""
          prop="desc"
        >
          <a-textarea v-model="artInfo.desc"></a-textarea>
        </a-form-model-item>

        <a-form-model-item
          label=""
          prop="img"
        >
          <a-upload
            listType="picture"
            name="file"
            :action="uploadUrl"
            :headers="headers"
            :defaultFileList="fileList"
            @change="handleUpload"
          >
            <a-button>
              <a-icon type="upload" />
            </a-button>
            <br />

            <template v-if="this.id">
              <img
                :src="artInfo.image"
                style="height:100px"
              />
            </template>
          </a-upload>
        </a-form-model-item>

        <a-form-model-item
          label=""
          prop="content"
        >
          <Editor
            v-model="artInfo.content"
            placeholder=" input "
          ></Editor>
        </a-form-model-item>

        <a-form-model-item>
          <!--[!]flex -->
          <a-button
            type="primary"
            style="margin-right: 15px"
            @click="handleSubmit(artInfo.id)"
          >{{artInfo.id?"":""}}</a-button>
          <a-button @click="handleCancel"></a-button>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>
  
<script>
import { Url } from "../../plugin/axios";
import Editor from "../editor/editor.vue";

export default {
  name: "AdminIndex",
  props: ["id"],
  data() {
    return {
      artInfo: {
        id: 0,
        title: "",
        cid: undefined,
        desc: "",
        content: "",
        image: "",
      },
      cateList: [],
      uploadUrl: Url + "upload",
      headers: {},
      fileList: [],
      rules: {
        //
        title: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 100,
            message: "3~100",
            trigger: "change",
          },
        ],
        desc: [],
        img: [],
        content: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },
  mounted() {
    this.getCateList();
    this.headers = {
      Authorization: `Bearer ${localStorage.getItem("token")}`,
    };
    if (this.id) {
      //this.id 
      this.getArtInfo(this.id);
    }
  },
  methods: {
    //
    async getCateList() {
      //datares.
      const { data: res } = await this.$axios.get("categories");
      if (res.status != 200) return this.$message.error(res.message);
      //
      this.cateList = res.data;
    },
    //
    async getArtInfo(id) {
      const { data: res } = await this.$axios.get(`article/info/${id}`);
      if (res.status != 200) return this.$message.error(res.message);
      (this.artInfo = res.data),
        (this.artInfo.id = res.data.ID),
        console.log(this.artInfo);
    },
    //v-model artInfo.cid, a-select  @change

    //
    handleUpload(info) {
      console.table(info);
      if (info.file.status !== "uploading") {
        console.log(info.file, info.fileList);
      }
      if (info.file.status === "done") {
        this.$message.success(`${info.file.name} `);
        this.artInfo.image = info.file.response.url; //
      } else if (info.file.status === "error") {
        this.$message.error(`${info.file.name} `);
        this.$message.error(this.uploadUrl);
      }
    },
    //
    handleSubmit(artId) {
      this.$refs.artInfoRef.validate(async (valid) => {
        if (valid) {
          if (artId) {
            //
            // console.log("artId: " + artId + "  ");
            const { data: res } = await this.$axios.put(
              `article/${artId}`,
              this.artInfo
            );
            if (res.status != 200)
              return this.$message.error(" " + res.message);
            this.$message.success("");
            this.$router.push("/artlist"); //
          } else {
            //
            // console.log("artId: " + artId + "  ");
            const { data: res } = await this.$axios.post(
              "article/add",
              this.artInfo
            );
            if (res.status != 200)
              return this.$message.error(" " + res.message);
            this.$message.success("");
            this.$router.push("/artlist");
          }
        }
      });
    },
    //
    handleCancel() {
      console.log("handleCancel");
      //
      if (this.id) {
        this.$message.info("");
      } else {
        this.$refs.artInfoRef.resetFileds();
        this.$message.info("");
      }
      //
      this.$router.push("/artlist");
    },
  },
  components: { Editor },
};
</script>
  
