<template>
  <div>
    <a-card>
      <!---->
      <a-row :gutter="16">
        <a-col :span="6">
          <a-input-search
            placeholder=""
            enter-button
            v-model="queryParam.username"
            @search="getUserList"
            allowClear
          />
        </a-col>
        <a-col :span="4">
          <!--vue<template><script>this.-->
          <a-button
            type="primary"
            @click="addUserVisible = true"
          ></a-button>
        </a-col>
      </a-row>
      <!--/-->

      <!---->
      <a-table
        :dataSource="userList"
        :columns="columns"
        :pagination="pagination"
        bordered
        rowKey="ID"
        @change="handleTableChange"
      >
        <!-- => slot<Col> columns[]key------------------------------------------------->

        <!--1./  <a-table> <Col> slot <Col> scopedSlots  -->
        <!--2.role<Col> <a-table>slot-scoped <a-table> -->
        <span
          slot="role"
          slot-scope="role"
        >{{role==1?'':''}}</span>

        <!--1.<a-table><Col> "action" <template> 3 -->
        <!--2.<Row>  <a-table>  <a-table> data -->

        <template
          slot="action"
          slot-scope="data"
        >
          <div class="actionSlot">
            <a-button
              type="primary"
              @click="editUser(data)"
              style="margin-right: 15px"
            ></a-button>

            <a-button
              type="danger"
              @click="deleteConfirm(data.ID)"
              style="margin-right: 15px"
            ></a-button>

            <a-button @click="resetPassword(data)"></a-button>
          </div>
        </template>
        <!-- => slot<Col>columns[]key------------------------------------------------->
      </a-table>
      <!--/-->
    </a-card>

    <!-- -->
    <a-modal
      title=""
      :visible="addUserVisible"
      @ok="handleAddUserOK"
      @cancel="handleAddUserCancel"
    >
      <a-form-model
        :model="userInfo"
        :rules="rules"
        ref="addUserRef"
        :label-col="labelCol"
        :wrapper-col="wrapperCol"
      >
        <a-form-model-item
          label=""
          prop="username"
        >
          <a-input v-model="userInfo.username"></a-input>
        </a-form-model-item>
        <a-form-model-item
          label=""
          prop="password"
          has-feedback
        >
          <a-input-password v-model="userInfo.password"></a-input-password>
        </a-form-model-item>
        <a-form-model-item
          label=""
          prop="checkPass"
          has-feedback
        >
          <a-input-password v-model="userInfo.checkPass"></a-input-password>
        </a-form-model-item>
        <a-form-model-item label="">
          <!--v-modelchange-->
          <a-select
            default-value=""
            style="width: 120px"
            @change="addUserRoleChange"
          >
            <a-select-option
              key="1"
              value="1"
            ></a-select-option>
            <a-select-option
              key="2"
              value="2"
            ></a-select-option>
          </a-select>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
    <!--/ -->

    <!-- -->
    <a-modal
      title=""
      :visible="editUserVisible"
      @ok="handleEditUserOK"
      @cancel="handleEditUserCancel"
    >
      <a-form-model
        :model="userEdit"
        :rules="rules"
        ref="editUserRef"
        :label-col="labelCol"
        :wrapper-col="wrapperCol"
      >
        <a-form-model-item
          label=""
          prop="username"
        >
          <a-input v-model="userEdit.username"></a-input>
        </a-form-model-item>

        <a-form-model-item label="">
          <!--v-modelchange-->
          <a-select
            :default-value="userEdit.role"
            style="width: 120px"
            @change="editUserRoleChange"
          >
            <a-select-option
              key="1"
              :value="1"
            ></a-select-option>
            <a-select-option
              key="2"
              :value="2"
            ></a-select-option>
          </a-select>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
    <!--/ -->

    <!-- -->
    <a-modal
      title=""
      :visible="resetPassVisible"
      @ok="handleResetPassOK"
      @cancel="handleResetPassCancel"
    >
      <a-form-model
        :model="resetPass"
        :rules="resetPassRules"
        ref="resetPassRef"
        :label-col="labelCol"
        :wrapper-col="wrapperCol"
      >
        <!-- resetPass.usernamelabelJS DOM -->
        <a-form-model-item label="">
          <span>{{resetPass.username}}</span>
        </a-form-model-item>

        <a-form-model-item
          label=""
          prop="password"
          has-feedback
        >
          <a-input-password v-model="resetPass.password"></a-input-password>
        </a-form-model-item>
        <a-form-model-item
          label=""
          prop="checkPass"
          has-feedback
        >
          <a-input-password v-model="resetPass.checkPass"></a-input-password>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
    <!--/ -->
  </div>
</template>
    
<script>
const columns = [
  {
    title: "ID",
    dataIndex: "ID", //jsonjson
    sorter: true,
    width: "10%",
    align: "center",
    key: "id",

    scopedSlots: { customRender: "id" }, //Vue<slot name="xxx">
  },
  {
    title: "",
    dataIndex: "username",
    align: "center",
    width: "20%",
    key: "username",
  },
  {
    title: "",
    dataIndex: "role",
    width: "20%",
    align: "center",
    key: "role",
    scopedSlots: { customRender: "role" }, //slot="role"
  },
  {
    title: "",
    width: "30%",
    align: "center",
    key: "action",
    scopedSlots: { customRender: "action" },
  },
];

export default {
  name: "AdminIndex",

  data() {
    // 
    let validatePass = (rule, value, callback) => {
      if (value == "") {
        callback(new Error(""));
      } else if (value.length < 6 || value.length > 20) {
        callback(new Error(" 6~20 "));
      } else {
        //  
        callback();
      }
    };

    // 
    let validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.userInfo.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    // userInfo{} -> resetPass{}
    let validateResetPass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.resetPass.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };

    return {
      //
      userList: [],

      //
      columns,

      //,getUserList() axios 
      queryParam: {
        username: "",
        pagesize: 5,
        pagenum: 1,
      },

      //visible: false, //visible
      addUserVisible: false, ///
      editUserVisible: false, ///
      resetPassVisible: false, ///

      //
      pagination: {
        pageSizeOptions: ["5", "10", "20"], //
        pageSize: 5, ///
        current: 1, //
        total: 0, //
        showSizeChanger: true, //
        showTotal: (total, range) => {
          //
          //range[0]
          //range[1]
          return ` ${total}   `;
        },
      },

      //
      userInfo: {
        id: 0,
        username: "",
        password: "",
        checkPass: "",
        role: 2,
      },
      //
      userEdit: {
        id: 0,
        username: "",
        password: "",
        checkPass: "",
        role: 0,
      },
      //
      resetPass: {
        id: 0,
        username: "",
        password: "",
        checkPass: "",
      },
      //&
      labelCol: { span: 6 },
      wrapperCol: { span: 14, offset: 1 },
      //&
      rules: {
        //+  
        //
        username: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 4,
            max: 12,
            message: "4-12",
            trigger: "blur",
          },
        ],
        password: [{ validator: validatePass, trigger: "change" }],
        checkPass: [{ validator: validatePass2, trigger: "change" }],
      },
      //
      resetPassRules: {
        password: [{ validator: validatePass, trigger: "change" }],
        checkPass: [{ validator: validateResetPass2, trigger: "change" }],
      },
    };
  },

  created() {
    //
    this.getUserList();
  },

  methods: {
    //(&&)
    // @@tip,@search (value,event)
    async getUserList() {
      //datares.
      const { data: res } = await this.$axios.get("users", {
        params: {
          username: this.queryParam.username, //""
          pagenum: this.queryParam.pagenum,
          pagesize: this.queryParam.pagesize,
        },
      });

      if (res.status != 200) return this.$message.error(res.message);

      //
      this.userList = res.data;
      this.pagination.total = res.total;
      // console.log("", res.total);
    },

    //Table (onChange antdv API,)
    handleTableChange(pagination, filters, sorter) {
      /* pagination 
         current:
         pageSize:
         total: 
      */

      // pager 
      // this.pagination 
      let pager = { ...this.pagination };

      //1. pagination 
      pager.current = pagination.current;
      pager.pageSize = pagination.pageSize;

      //2. pagination 
      this.queryParam.pagenum = pagination.current;
      this.queryParam.pagesize = pagination.pageSize;

      //3.==
      if (pagination.pageSize !== this.pagination.pageSize) {
        pager.current = 1;
        this.queryParam.pagenum = 1;
      }

      //4.
      this.pagination = pager;

      //5.
      this.getUserList();
    },
    //==========================================
    //
    async deleteUser(id) {
      // <a-table> data getUserList()
      /*  CreatedAt
          DeletedAt
          ID
          UpdatedAt
          password
          role
          username
      */
      // console.log("<a-table>:", data);
      const res = await this.$axios.delete(`user/${id}`);
      if (res.status !== 200) {
        return this.$message.error(res.message);
      }
      this.$message.success("");

      //5.
      this.getUserList();
    },
    //,deleteUser()
    deleteConfirm(id) {
      this.$confirm({
        title: "?",
        content: "",
        okText: "",
        okType: "primary",
        cancelText: "",
        onOk: () => {
          this.deleteUser(id); //this Vue this 
        },
        onCancel: () => {
          this.$message.info("");
        },
      });
    },
    //==========================================
    // 
    handleAddUserOK() {
      // validate 
      this.$refs.addUserRef.validate(async (valid) => {
        if (!valid) return this.$message.error(""); // return
        //axios API
        const { data: res } = await this.$axios.post("user/add", {
          username: this.userInfo.username,
          password: this.userInfo.password,
          role: this.userInfo.role,
        });
        if (res.status !== 200) return this.$message.error(res.message);

        this.addUserVisible = false; //
        this.$refs.addUserRef.resetFields(); //ref
        this.$message.success(""); //
        this.getUserList(); //
      });
    },
    // 
    handleAddUserCancel() {
      // model{} 

      this.addUserVisible = false;
      this.$refs.addUserRef.resetFields();
      this.$message.info("");
    },
    // ""
    addUserRoleChange(val) {
      // userInfo 
      this.userInfo.role = val;
      console.log(this.userInfo.role);
    },
    //==========================================
    //
    editUser(data) {
      // console.log(data); //data

      //1. data v-model
      this.userEdit.id = data.ID;
      this.userEdit.username = data.username;
      this.userEdit.password = data.password;
      this.userEdit.role = data.role;

      //2.
      this.editUserVisible = true;

      //3.
    },
    // 
    handleEditUserOK() {
      // validate 
      this.$refs.editUserRef.validate(async (valid) => {
        if (!valid) return this.$message.error(""); // return

        //axios API
        const { data: res } = await this.$axios.put(
          `user/${this.userEdit.id}`,
          {
            username: this.userEdit.username,
            role: this.userEdit.role,
          }
        );

        if (res.status !== 200) return this.$message.error(res.message);
        this.editUserVisible = false; //
        // this.$refs.editUserRef.resetFields(); //
        this.$message.success(""); //
        this.getUserList(); //
      });
    },
    // 
    handleEditUserCancel() {
      this.editUserVisible = false;
      // this.$refs.editUserRef.resetFields();//
      this.$message.info("");
    },
    // ""
    editUserRoleChange(val) {
      // userInfo 
      this.userEdit.role = val;
    },
    //==========================================
    // 
    resetPassword(data) {
      //1. data v-model
      this.resetPass.id = data.ID;
      this.resetPass.username = data.username;
      // this.resetPass.password = data.password;//

      //2.
      this.resetPassVisible = true;
    },
    handleResetPassOK() {
      //1. resetPassRules 
      this.$refs.resetPassRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");

        //2. PUT 
        const res = await this.$axios.put(`user/resetpw/${this.resetPass.id}`, {
          password: this.resetPass.password,
        });

        //3.
        if (res.status !== 200) return this.$message.error(res.message);

        this.resetPassVisible = false; //
        this.$refs.resetPassRef.resetFields();
        this.$message.success(""); //
        this.getUserList(); //
      });
    },
    handleResetPassCancel() {
      this.resetPassVisible = false;
      this.$refs.resetPassRef.resetFields();
      this.$message.info("");
    },
  },
};
</script>
    
<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>
