<template>
  <div>
    <a-card>
      <!---->
      <a-row :gutter="16">
        <a-col :span="6">
          <a-input-search
            placeholder=""
            enter-button
            v-model="queryParam.name"
            @search="getCateList"
            allowClear
          />
        </a-col>
        <a-col :span="4">
          <a-button
            type="primary"
            @click="addCateVisible = true"
          ></a-button>
        </a-col>
      </a-row>
      <!--/-->

      <!---->
      <a-table
        :dataSource="cateList"
        :columns="columns"
        :pagination="pagination"
        bordered
        rowKey="ID"
        @change="handleTableChange"
      >
        <template
          slot="action"
          slot-scope="data"
        >
          <div class="actionSlot">
            <a-button
              type="primary"
              @click="editCate(data)"
              style="margin-right: 15px"
            ></a-button>

            <a-button
              type="danger"
              @click="deleteConfirm(data.id)"
              style="margin-right: 15px"
            ></a-button>
          </div>
        </template>
      </a-table>
      <!--/-->
    </a-card>

    <!-- -->
    <a-modal
      title=""
      :visible="addCateVisible"
      @ok="handleAddCateOK"
      @cancel="handleAddCateCancel"
    >
      <a-form-model
        :model="cateInfo"
        :rules="rules"
        ref="addCateRef"
        :label-col="labelCol"
        :wrapper-col="wrapperCol"
      >
        <a-form-model-item
          label=""
          prop="name"
        >
          <a-input v-model="cateInfo.name"></a-input>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
    <!--/ -->

    <!-- -->
    <a-modal
      title=""
      :visible="editCateVisible"
      @ok="handleEditCateOK"
      @cancel="handleEditCateCancel"
    >
      <a-form-model
        :model="cateEdit"
        :rules="rules"
        ref="editCateRef"
        :label-col="labelCol"
        :wrapper-col="wrapperCol"
      >
        <a-form-model-item
          label=""
          prop="name"
        >
          <a-input v-model="cateEdit.name"></a-input>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
    <!--/ -->
  </div>
</template>
    
<script>
const columns = [
  {
    title: "ID",
    dataIndex: "id", //jsonjson
    sorter: true,
    width: "10%",
    align: "center",
    key: "id",

    scopedSlots: { customRender: "id" }, //Vue<slot name="xxx">
  },
  {
    title: "",
    dataIndex: "name",
    align: "center",
    width: "30%",
    key: "name",
  },
  {
    title: "",
    width: "20%",
    align: "center",
    key: "action",
    scopedSlots: { customRender: "action" },
  },
];

export default {
  name: "AdminIndex",

  data() {
    return {
      //
      cateList: [],

      //
      columns,

      //,getCateList() axios 
      queryParam: {
        name: "",
        pagesize: 5,
        pagenum: 1,
      },

      //visible: false, //visible
      addCateVisible: false, ///
      editCateVisible: false, ///

      //
      pagination: {
        pageSizeOptions: ["5", "10", "20"], //
        pageSize: 5, ///
        current: 1, //
        total: 0, //
        showSizeChanger: true, //
        showTotal: (total, range) => {
          //
          //range[0]
          //range[1]
          return ` ${total}   `;
        },
      },

      //
      cateInfo: {
        id: 0,
        name: "",
      },
      //
      cateEdit: {
        id: 0,
        name: "",
      },
      //&
      labelCol: { span: 6 },
      wrapperCol: { span: 14, offset: 1 },
      //&
      rules: {
        name: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 1,
            max: 12,
            message: "1-12",
            trigger: "blur",
          },
        ],
      },
    };
  },

  created() {
    //
    this.getCateList();
  },

  methods: {
    //(&&)
    async getCateList() {
      //datares.
      const { data: res } = await this.$axios.get("categories", {
        params: {
          name: this.queryParam.name, //""
          pagenum: this.queryParam.pagenum,
          pagesize: this.queryParam.pagesize,
        },
      });

      if (res.status != 200) return this.$message.error(res.message);

      //
      this.cateList = res.data;
      this.pagination.total = res.total;
      // console.log("", res.total);
    },

    //Table (onChange antdv API,)
    handleTableChange(pagination, filters, sorter) {
      /* pagination 
         current:
         pageSize:
         total: 
      */

      // pager 
      // this.pagination 
      let pager = { ...this.pagination };

      //1. pagination 
      pager.current = pagination.current;
      pager.pageSize = pagination.pageSize;

      //2. pagination 
      this.queryParam.pagenum = pagination.current;
      this.queryParam.pagesize = pagination.pageSize;

      //3.==
      if (pagination.pageSize !== this.pagination.pageSize) {
        pager.current = 1;
        this.queryParam.pagenum = 1;
      }

      //4.
      this.pagination = pager;

      //5.
      this.getCateList();
    },
    //==========================================
    //
    async deleteCate(id) {
      console.log("id:", id);
      const res = await this.$axios.delete(`category/${id}`);
      if (res.status !== 200) {
        return this.$message.error(res.message);
      }
      this.$message.success("");

      //5.
      this.getCateList();
    },
    //,deleteCate()
    deleteConfirm(id) {
      this.$confirm({
        title: "?",
        content: "",
        okText: "",
        okType: "primary",
        cancelText: "",
        onOk: () => {
          this.deleteCate(id); //this Vue this 
        },
        onCancel: () => {
          this.$message.info("");
        },
      });
    },
    //==========================================
    // 
    handleAddCateOK() {
      // validate 
      this.$refs.addCateRef.validate(async (valid) => {
        if (!valid) return this.$message.error(""); // return
        //axios API
        const { data: res } = await this.$axios.post("category/add", {
          name: this.cateInfo.name,
        });
        if (res.status !== 200) return this.$message.error(res.message);

        this.addCateVisible = false; //
        this.$refs.addCateRef.resetFields(); //ref
        this.$message.success(""); //
        this.getCateList(); //
      });
    },
    // 
    handleAddCateCancel() {
      // model{} 

      this.addCateVisible = false;
      this.$refs.addCateRef.resetFields();
      this.$message.info("");
    },
    //==========================================
    //
    editCate(data) {
      // console.log(data); //data

      //1. data v-model
      this.cateEdit.id = data.id;
      this.cateEdit.name = data.name;

      //2.
      this.editCateVisible = true;

      //3.
    },
    // 
    handleEditCateOK() {
      // validate 
      this.$refs.editCateRef.validate(async (valid) => {
        if (!valid) return this.$message.error(""); // return

        //axios API
        const { data: res } = await this.$axios.put(
          `category/${this.cateEdit.id}`,
          {
            name: this.cateEdit.name,
          }
        );

        if (res.status !== 200) return this.$message.error(res.message);
        this.editCateVisible = false; //
        this.$message.success(""); //
        this.getCateList(); //
      });
    },
    // 
    handleEditCateCancel() {
      this.editCateVisible = false;
      this.$message.info("");
    },
  },
};
</script>
    
<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>
