<template>
  <div class="container">
    <div class="loginBox">
      <a-form-model
        ref="loginFormRef"
        class="loginForm"
        :model="formData"
        :rules="rules"
        :label-col="labelCol"
        :wrapper-col="wrapperCol"
      >
        <a-form-model-item
          label=""
          prop="username"
        >
          <!-- item  prop :,itemprop formDatarules -->
          <a-input
            placeholder=""
            v-model="formData.username"
          >
            <a-icon
              slot="prefix"
              type="user"
              style="color: rgba(0,0,0,.45)"
            />
          </a-input>
        </a-form-model-item>

        <a-form-model-item
          label=""
          prop="password"
        >
          <a-input
            placeholder=""
            type="password"
            v-model="formData.password"
            @keyup.enter="submitForm('loginFormRef')"
          >
            <a-icon
              slot="prefix"
              type="lock"
              style="color: rgba(0,0,0,.45)"
            />
          </a-input>
        </a-form-model-item>

        <a-form-model-item class="loginBtn">
          <a-button
            type="primary"
            style="margin-right:20px"
            @click="submitForm('loginFormRef')"
          ></a-button>
          <a-button
            type="info"
            @click="resetForm('loginFormRef')"
          ></a-button>
        </a-form-model-item>
      </a-form-model>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      labelCol: { span: 4 },
      wrapperCol: { span: 20 },

      //
      formData: {
        username: "",
        password: "",
      },

      //
      rules: {
        username: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 4,
            max: 12,
            message: "4-12",
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 6,
            max: 20,
            message: "6-20",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    // formName 
    //
    submitForm(formName) {
      //rules 
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          try {
            //data res
            const { data: res } = await this.$axios.post(
              "/login",
              this.formData
            );
            if (res.status != 200) {
              return this.$message.error(res.message);
            } else {
              //token .session token
              // window.sessionStorage.setItem("token", res.token);
              window.localStorage.setItem("token", res.token);
              this.$router.push("/index");
            }
          } catch (err) {
            console.log(err);
          }
        } else {
          this.$message.error(""); //message
          this.$refs[formName].resetFields(); //
        }
      });
    },
    //
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>


<style scoped>
.container {
  height: 100%;
  background: #282c34;
  /*flex  
  display: flex;
  justify-content: center;
  align-items: center; */
}
.loginBox {
  width: 450px;
  height: 300px;
  background: #ffffff;
  /* , */
  position: absolute;
  top: 50%;
  left: 70%;
  transform: translate(-50%, -50%);

  border-radius: 10px;
}
.loginForm {
  width: 100%;
  position: absolute;
  bottom: 10%;
  padding: 0 20px;
  /*  = border-left + padding-left + width + border-right + padding-right  */
  /*  = border-top + padding-top + height + border-bottom + padding-bottom  */
  box-sizing: border-box; /* = width   = height */
}
.loginBtn {
  display: flex;
  justify-content: flex-end;
}
</style>